import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import Image from "next/image";

const newsData = [
  { id: 1, title: "Tech Innovations Shaping 2025", description: "From AI to Web3, explore how emerging technologies are transforming industries across the globe.", image: "/news1.png" },
  { id: 2, title: "SLIIT Launches New IT Program", description: "The program aims to produce industry-ready graduates skilled in cloud computing and full-stack development.", image: "/news2.png" },
  { id: 3, title: "Developers Embrace Next.js 15", description: "Next.js continues to redefine frontend performance with powerful server-side rendering features.", image: "/news3.png" },
];

export default function NewsSection() {
  const [cards, setCards] = useState(newsData);

  useEffect(() => {
    // Only animate on tablet/laptop
    if (window.innerWidth >= 640) {
      const interval = setInterval(() => {
        setCards(prev => [...prev.slice(1), prev[0]]); // move first to last
      }, 4000); // every 4 seconds
      return () => clearInterval(interval);
    }
  }, []);

  return (
    <section className="py-16 px-2 sm:px-40 bg-gray-50 dark:bg-[#0a0a0a]" id="news">
      <div className="max-w-7xl mx-auto text-center mb-12">
        <h2 className="text-4xl font-extrabold text-gray-900 dark:text-white">
          Latest <span className="text-orange-500">News</span>
        </h2>
        <p className="text-gray-600 dark:text-gray-300 mt-3">
          Stay updated with our latest announcements and updates
        </p>
      </div>

      <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-8">
        {cards.map((item, index) => (
          <motion.div
            key={item.id}
            layout
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: index * 0.1 }}
            className="bg-white dark:bg-gray-900 rounded-2xl shadow-lg overflow-hidden hover:shadow-xl transition-all"
          >
            <div className="relative w-full h-56">
              <Image
                src={item.image}
                alt={item.title}
                fill
                className="object-cover hover:scale-105 transition-transform duration-500"
              />
            </div>
            <div className="p-5">
              <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-2">
                {item.title}
              </h3>
              <p className="text-gray-600 dark:text-gray-300 text-sm">
                {item.description}
              </p>
            </div>
          </motion.div>
        ))}
      </div>
    </section>
  );
}
